.books-for-sale-list {
	max-width: 100vw;
	margin: 0 auto;
	display: flex;
	overflow-x: auto;
	gap: 10px;
	margin: 0 auto;
	white-space: nowrap;
	padding: 0 30px;
	scroll-behavior: smooth;
	position: relative;
}

.book-image-button {
	flex-shrink: 0;
	width: 260px;
	height: 360px;
	background: linear-gradient(0deg, #171717 -7.05%, rgba(0, 0, 0, 0.00) 87.18%, rgba(16, 15, 15, 0.00) 87.18%), lightgray 50% / cover no-repeat;
	cursor: pointer;
	border-radius: 15px; 
	transition: transform 0.3s;
	position: relative;
	overflow: hidden;
}

.book-image-button img {
	width: 100%;
	height: 100%;
	object-fit: cover;
	display: block;
}


.book-image-button:hover {
	transform: scale(1.05);
}

.scroll-button {
	background: #C69A52;
	border-radius: 50px;
	width: 40px;
	height: 40px;
	border: none;
	cursor: pointer;
	display: flex;
	align-items: center;
	justify-content: center;
	position: absolute;
	z-index: 10;
	bottom: -50px;
}

.scroll-button[left] {
	left: 30px;
}

.scroll-button[right] {
	right: 30px;
}